}
html{height:100%;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;-webkit-box-sizing:border-box;box-sizing:border-box
}
*,::before,::after{-webkit-box-sizing:inherit;box-sizing:inherit
}
