/*Body
------------------*/
body{
	width:100%;
	min-height:100%;
	color:#fff;
	background-color:#2E2A29;
	font-family:'Montserrat',sans-serif;
	font-weight:400
}
/*Header
------------------*/
header{
	width:100%;
	-webkit-box-shadow:0 2px 2px rgba(0,0,0,.2);
	        box-shadow:0 2px 2px rgba(0,0,0,.2);
	margin-bottom:2em
}
header img{max-height:120px;margin:auto
}
/*Content
-------------*/
main{
	margin:0 2em
}
section h1,section h2{
	text-align:center;
	margin:1em auto;
	padding:1em;
	font-weight:500
}
#price{
	color:#333;
	background:#ececec;
	border-radius:.5em;
	min-height:80vh;
	width:50%;
	margin:auto;
}
/*Columns
-------------*/
.cl{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-wrap:wrap;
	    flex-wrap:wrap;
	-webkit-box-orient:horizontal;
	-webkit-box-direction:normal;
	    -ms-flex-direction:row;
	        flex-direction:row;
	-webkit-box-pack:justify;
	    -ms-flex-pack:justify;
	        justify-content:space-between;
	-webkit-box-align:stretch;
	    -ms-flex-align:stretch;
	        align-items:stretch;
}
/*Price Calculator
-------------*/
#priceCalculator{
	position:fixed;
	position:-webkit-sticky;
	position:sticky;
	top:40px;
	background:#c2b49a;
	width:20%;
	border-radius:6px;
	padding:1em 2em 2em;
	display:inline-block;
}
/*Forms
-------------*/
form.calcForm{
	width:50%;
	margin:auto;
	-webkit-touch-callout:none;
	  -webkit-user-select:none;
		 -moz-user-select:none;
		  -ms-user-select:none;
			  user-select:none;
}
.calcForm label{
	display:block;
	margin:1.5em 0 .8em;
}
form ul{
	list-style:none;
	padding:0;
}
ul#features{
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-wrap:wrap;
	    flex-wrap:wrap;
	-webkit-box-orient:horizontal;
	-webkit-box-direction:normal;
	    -ms-flex-direction:row;
	        flex-direction:row;
	-webkit-box-pack:start;
	    -ms-flex-pack:start;
	        justify-content:flex-start;
	-webkit-box-align:stretch;
	    -ms-flex-align:stretch;
	        align-items:stretch;
}
ul#features li{width:50%
}
/*Option Select*/
.selectbox{
	position:relative;
	display:inline-block;
	vertical-align:middle;
}
.selectbox select{
	background-color:#c2b49a;
	color:#fff;
	font-size:inherit;
	padding:.5em;
	padding-right:3em;
	min-width:200px;
	border:0;
	margin:0;
	border-radius:6px;
	-webkit-appearance:button;
	   -moz-appearance:button;
	cursor:pointer;
}
.selectbox::before,.selectbox::after{
	content:"";
	position:absolute;
	pointer-events:none;
}
.selectbox::after{
	content:"\F0DD";
	font-family:"FontAwesome";
	color:#fff;
	font-size:1.3em;
	line-height:.7;
	right:.5em;
	top:50%;
	margin-top:-.5em;
}
.selectbox::before{
	width:2em;
	right:0;
	top:0;
	bottom:0;
	border-radius:0 6px 6px 0;
	background-color:rgba(0,0,0,0.2);
	-webkit-transition:all .3s ease-out;
			transition:all .3s ease-out;
}
.selectbox:hover::before{opacity:.8
}
.selectbox select[disabled]{color:rgba(0,0,0,.3)
}
.selectbox select[disabled]::after{color:rgba(0,0,0,.1)
}
/*Number Input*/
input[type="number"]{
	position:relative;
	background:#c2b49a;
	border:none;
	color:#fff;
	border-radius:6px;
	padding:.5em;
	-webkit-appearance:button;
	   -moz-appearance:button;
}
input[type="number"]::-webkit-outer-spin-button, 
input[type="number"]::-webkit-inner-spin-button{
	-webkit-appearance:none;
	background:rgba(0,0,0,0.2)url(data:image/svg+xml;base64,) no-repeat center center;
	border-radius:0 6px 6px 0;
	width:1em;
	opacity:1;
	position:absolute;
	top:0;
	right:0;
	bottom:0;
	-webkit-transition:all .3s ease-out;
			transition:all .3s ease-out;
	cursor:pointer;
	padding:0 2px
}
input[type="number"]::-webkit-inner-spin-button:hover,input[type="number"]::-webkit-inner-spin-button:active{opacity:.8
}
/*Checkboxes and Radio Buttons*/
[type="checkbox"]:not(:checked),[type="checkbox"]:checked,[type="radio"]:not(:checked),[type="radio"]:checked{
	position:absolute;
	left:-9999px
}
[type="checkbox"]:not(:checked) + label,[type="checkbox"]:checked + label,[type="radio"]:not(:checked) + label,[type="radio"]:checked + label{
	position:relative;
	padding-left:35px;
	cursor:pointer;
	display:inline-block;
	text-transform:capitalize;
	font-size:15px
}
[type="checkbox"]:not(:checked) + label:before,[type="checkbox"]:checked + label:before,[type="checkbox"]:not(:checked) + label:after,[type="checkbox"]:checked + label:after,[type="radio"]:not(:checked) + label:before,[type="radio"]:checked + label:before,[type="radio"]:not(:checked) + label:after,[type="radio"]:checked + label:after{
	content:'';
	position:absolute
}
[type="checkbox"]:not(:checked) + label:before,[type="checkbox"]:checked + label:before,[type="radio"]:not(:checked) + label:before,[type="radio"]:checked + label:before{
	left:0;
	top:-4px;
	width:25px;
	height:25px;
	border:2px solid #c2b49a;
	border-radius:6px;
	-webkit-transition:background-color .2s;
	transition:background-color .2s
}
[type="radio"]:not(:checked) + label:before,[type="radio"]:checked + label:before{
	border-radius:100%;
}
[type="checkbox"]:checked + label:before,[type="radio"]:checked + label:before{
	background:#c2b49a;
	border:2px solid #c2b49a
}
[type="checkbox"]:checked + label:before,[type="checkbox"]:not(:checked) + label:before,[type="radio"]:checked + label:before,[type="radio"]:not(:checked) + label:before{
	content:"";
	display:inline-block;
	width:25px;
	height:25px;
	position:absolute;
	background-repeat:no-repeat;
	background-position:center center;
	background-size:80%
}
[type="checkbox"]:checked + label:before,[type="radio"]:checked + label:before{
	background-image:url(data:image/svg+xml;base64,)
}
[type="checkbox"]:checked + label:after,[type="radio"]:checked + label:after{
	border:0;
	outline:0;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box
}
/*ToolTip*/
[tooltip-info]{
	position:relative;
	display:inline-block;
}
[tooltip-info]:after{
	content:attr(tooltip-info);
	display:inline-block;
	position:absolute;
	background:#444;
	min-width:14vw;
	width:auto;
	color:#fff;
	text-align:center;
	margin:auto;
	bottom:40px;
	left:-6.5vw;
	right:0;
	border-radius:5px;
	padding:.5em 1em;
	opacity:0;
	z-index:10;
	pointer-events:none;
	-webkit-transition:all .3s ease-out;
			transition:all .3s ease-out;
}
[tooltip-info]:before{
	content:'';
	width:0;
	height:0;
	opacity:0;
	border-left:15px solid transparent;
	border-right:15px solid transparent;
	border-top:15px solid #444;
	pointer-events:none;
	-webkit-transition:all .3s ease-out;
			transition:all .3s ease-out;
	position:absolute;
	z-index:10;
	bottom:25px;
	left:165px;
	left:calc(50% - 15px);
}
[tooltip-info]:hover:before,[tooltip-info]:hover:after{
	opacity:1;
	pointer-events:auto;
}
/*Footer
-------------*/
footer{
	font-weight:300;
	width:100%;
	text-align:center;
	margin:1em auto;
}
/*ToTop
---------------*/
#totop{
	color:#fff;
	padding:6px 7px;
	position:fixed;
	bottom:3%;
	right:2%;
	z-index:999;
	border-radius:5em;
	border:2px solid #c2b49a;
	background-color:#c2b49a;
	line-height:0;
	-webkit-transition:transform .3s ease,background .3s ease,color .3s ease;
			-webkit-transition:background .3s ease,color .3s ease,-webkit-transform .3s ease;
			transition:background .3s ease,color .3s ease,-webkit-transform .3s ease;
			transition:transform .3s ease,background .3s ease,color .3s ease;
			transition:transform .3s ease,background .3s ease,color .3s ease,-webkit-transform .3s ease;
}
#totop:after{
	content:"";
	display:block;
	background-image:url(data:image/svg+xml;base64,);
	background-size:cover;
	background-repeat:no-repeat;
	background-position-y:-2px;
	width:28px;
	height:28px;
}
#totop:hover{
	background-color:#fff;
	color:#c2b49a;
	-webkit-transform:scale(1.1);
			transform:scale(1.1);
}
#totop:hover:after{background-image:url(data:image/svg+xml;base64,)
}
/*Fade*/
.fade-enter-active,.fade-leave-active{-webkit-transition:all .5s;transition:all .5s
}
.fade-enter,.fade-leave-to{opacity:0
}
